.scrollbox {
  position: relative;
  overflow: hidden;
  width: 100%;
  
  .content {
    padding-right: calc(100% + 5px);
    width: calc(200% + 20px);
    -ms-flex-direction: column;
        flex-direction: column;
    overflow-y: scroll;
    max-height: 100%;
    display: -ms-flexbox;
    display: flex;

    & > * {
      -ms-flex-negative: 0;
          flex-shrink: 0;
    }
  }
    
  .scrollbar {
    border-left: 1px solid rgba(#000, .15);
    background-color: rgba(#fff, .5);
    position: absolute;
    height: 100%;
    right: 0;
    top: 0;
    
    &:hover {
      background-color: #fff;

      .thumb {
        width: 8px;
      }
    }
      
    &::before {
      position: absolute;
      height: 100%;
      left: -20px;
      content: '';
      width: 20px;
      top: 0;
    }
    
    .thumb {
      -webkit-transition: width .15s, height .3s;
      -o-transition: width .15s, height .3s;
      transition: width .15s, height .3s;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      cursor: pointer;
      width: 4px;
      left: 0;

      &:active {
        width: 8px;
      }
    }
  }
}


