$black: rgba(#000, .87);
$grey: rgba(#000, .54);
$white: #fff;
$blue: #1976D2;
$red: #f44336;
$green: #4CAF50;
$borderColor: #ddd;

$font-family: Roboto, sans-serif;
$font-size: 16px;

$lowElevation: 0 1px 1px rgba($black, .5);
$elevation1Shadow: 0 1px 3px rgba(#000, .5);
$elevation3Shadow: 0 3px 7px rgba(#000, .5);
$elevation5Shadow: 0 5px 15px rgba(#000, .5);

@mixin material-button {
  box-shadow: $elevation1Shadow;
  text-transform: uppercase;
  justify-content: center;
  font-family: inherit;
  border-radius: 2px;
  font-size: .875rem;
  line-height: 36px;
  user-select: none;
  font-weight: 500;
  cursor: pointer;
  transition: .2s;
  display: flex;
  border: none;

  &:hover {
    box-shadow: $elevation3Shadow;
    transform: translateY(-1px);
  }

  &:active {
    transform: translateY(1px);
    box-shadow: $lowElevation;
  }

  &:disabled {
    background-color: #e3e3e3;
    box-shadow: $lowElevation;
    pointer-events: none;
    color: $grey;
  }
}

@mixin card {
  box-shadow: $elevation1Shadow;
  border-radius: 2px;
  padding: 12px 16px;
}

@mixin pseudo($display: block, $pos: absolute, $content: '') {
  display: $display;
  content: $content;
  position: $pos;
}



