@import './default.scss';

.App {
  -ms-flex-pack: center;
      justify-content: center;
  background-color: #eee;
  -ms-flex-align: center;
      align-items: center;
  min-height: 100vh;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

[data-title] {
  position: relative;
  
  &:hover::after {
    top: calc(100% + 4px);
    visibility: visible;
    opacity: 1;
  }
  
  &::after {
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    -webkit-transition-timing-function: ease-out;
         -o-transition-timing-function: ease-out;
            transition-timing-function: ease-out;
    background-color: rgba(#000, .74);
    content: attr(data-title);
    top: calc(100% + 10px);
    -webkit-transition-delay: .3s;
         -o-transition-delay: .3s;
            transition-delay: .3s;
    display: inline-block;
    text-transform: none;
    white-space: nowrap;
    line-height: 1.4em;
    border-radius: 2px;
    position: absolute;
    visibility: hidden;
    padding: 0 .375em;
    font-weight: 400;
    font-size: .8em;
    height: 1.4em;
    z-index: 999;
    color: #fff;
    opacity: 0;
    left: 50%;
  }
}



